  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
@media (max-width: 1199px) {
  .hero__video {
    width: 442px;
    height: 300px;
    background-size: cover;
  }
}

.hero__video-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  padding: 0;
  border: none;
  outline: none;
  background-color: transparent;
}
.hero__video-play:hover {
  cursor: pointer;
}
.hero__video-play:hover:not(:disabled) circle, .hero__video-play:focus:not(:disabled) circle {
  stroke: #1c3374;
}
.hero__video-play:hover:not(:disabled) path, .hero__video-play:focus:not(:disabled) path {
  fill: #ffffff;
  stroke: #000000;
}
.hero__video-play:active:not(:disabled) circle {
  stroke: #1c3374;
  fill: #1c3374;
  fill-opacity: 1;
}
.hero__video-play:active:not(:disabled) path {
  fill: #ffffff;
  stroke: #ffffff;
}
.hero__video-play:disabled {
  pointer-events: none;
}
.hero__video-play:disabled circle {
  stroke: #0266c1;
  stroke-opacity: 0.1;
}
.hero__video-play:disabled path {
  fill: #0266c1;
  stroke: #0266c1;
  fill-opacity: 0.1;
  stroke-opacity: 0.1;
}

.hero__video-icon-play {
