.social {
  padding: 0;
  margin-bottom: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  max-width: 240px;
}

.social__link {
  display: block;
  position: relative;
  width: 30px;
  height: 30px;
  background-color: transparent;
  outline: 1px solid rgba(45, 56, 63, 0.3);
}
.social__link:hover:not(:disabled):not([aria-disabled=true]), .social__link:focus:not(:disabled):not([aria-disabled=true]), .social__link:focus-visible:not(:disabled):not([aria-disabled=true]) {
  outline: 1px solid #2d383f;
}
.social__link:active:not(:disabled):not([aria-disabled=true]) {
  outline: 1px solid #0266c1;
  background-color: #0266c1;
}
.social__link:active:not(:disabled):not([aria-disabled=true]) .social__icon {
  fill: #ffffff;
}
.social__link:disabled, .social__link[aria-disabled=true] {
  pointer-events: none;
  outline: 1px solid rgba(45, 56, 63, 0.3);
  border: none;
}
.social__link:disabled .social__icon, .social__link[aria-disabled=true] .social__icon {
  fill: rgba(45, 56, 63, 0.3);
}

.social__icon {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #2d383f;
}

